<template>
    <div class="index container">
        <div class="card horizontal showcase">
            <div class="card-image">
                <img src="../assets/martini.png" alt="">
            </div>
            <div class="card-stacked">
                <div class="card-content">
                    <p>A cocktail is an alcoholic mixed drink, which is either a combination of spirits, or one or more spirits mixed with other ingredients such as fruit juice, flavored syrup, or cream. There are various types of cocktails, based on the number and kind of ingredients added. The origins of the cocktail are debated.</p>
                    <br>
                    <p>There is a lack of clarity on the origins of cocktails. Traditionally cocktails were a mixture of spirits, sugar, water, and bitters. By the 1860s, however, a cocktail frequently included a liqueur. The first publication of a bartenders' guide which included cocktail recipes was in 1862 – How to Mix Drinks; or, The Bon Vivant's Companion, by "Professor" Jerry Thomas. In addition to recipes for punches, sours, slings, cobblers, shrubs, toddies, flips, and a variety of other mixed drinks were 10 recipes for "cocktails". A key ingredient differentiating cocktails from other drinks in this compendium was the use of bitters. Mixed drinks popular today that conform to this original meaning of "cocktail" include the Old Fashioned whiskey cocktail, the Sazerac cocktail, and the Manhattan cocktail.</p>
                </div>
            </div>
        </div>
       <div class="cards">
            <div class="card" v-for="cocktail in cocktails" :key="cocktail.id">
                <div class="card-content">
                    <i class="material-icons delete" @click="deleteCocktail(cocktail.id)">delete</i>
                    <h2 class="red-text">{{ cocktail.title }}</h2>
                    <ul class="ingredients">
                        <li v-for="(ing,index) in cocktail.ingredients" :key="index">
                            <span class="chip">
                                {{ ing }}
                            </span>
                        </li>
                    </ul>
                </div>
                <span class="btn-floating btn-large halfway-fab red">
                    <router-link :to="{ name: 'EditCocktail', params: { cocktail_slug: cocktail.slug } }">
                        <i class="material-icons edit">edit</i>
                    </router-link>
                </span>
            </div>
       </div>

    </div>
        
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'Index',
    data() {
        return {
            cocktails: [] 
        }
    },
    methods: {
        deleteCocktail(id) {
            // delete doc from firestore
            db.collection('cocktails').doc(id).delete().then(() => {
                this.cocktails = this.cocktails.filter(cocktail => {
                return cocktail.id != id
        })
    })
        }
    },
    created() {
        db.collection('cocktails').get()
        .then(snapshot => {
            snapshot.forEach(doc => {
                let cocktail = doc.data()
                cocktail.id = doc.id
                this.cocktails.push(cocktail)
            })
        })
    }
}
</script>

<style>
.showcase {
    margin-top: 60px;
}
.card-image {
    width: 300px;
    padding: 20px;
}
.cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
    margin-top: 60px;
}
.cards h2 {
    font-size: 1.8em;
    text-align: center;
    margin-top: 0;
}
.cards .ingredients {
    margin: 30px auto;
}
.cards .ingredients li {
    display: inline-block;
}
 .cards .delete {
    position: absolute;
    top: 4px;
    right: 4px;
    cursor: pointer;
    color: #aaa;
    font-size: 1.4em; 
  }

</style>



